/******************** LOADER ********************/
.site-loader {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: $white;
    display: flex;
    justify-content: center;
    align-items: center;
}


/******************** TOP BAR ********************/
.top-bar {
    z-index: 50;
    width: 100%;
    padding: 20px;
    transition: .2s ease;

    img {
        width: 320px;
    }

}

.top-bar-mobile {
    display: none;
    z-index: 50;
    width: 100%;
    padding: 15px;
    color: $white;

    i {
        font-size: 26px;
    }

    img {
        width: 200px;
    }
}

.top-bar-active {
    background: rgba(0 0 0/70%);
    backdrop-filter: blur(20px);
    box-shadow: 0 2px 10px rgba(0 0 0/20%);
}


/******************** Menu Mobile ********************/
.menu-mobile {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    -webkit-backdrop-filter: blur(30px);
    backdrop-filter: blur(30px);
    background: rgba(0 0 0 /50%);
    overflow: hidden;
    transition: width .2s ease;
    width: 0;

    .content-menu-mobile {
        width: 100%;
        height: 100%;

        .btn-close-menu-mobile {
            position: absolute;
            top: 20px;
            right: 20px;
            font-size: 26px;
            color: $white;
        }

        img {
            width: 230px;
        }

        a {
            font-size: 24px;
            color: $white;
        }
    }
}


/******************** Header ********************/
.header {
    position: relative;
    z-index: 21;
    width: 100%;
    height: 550px;
    background: url(../assets/img/bg-header.jpg);
    background-size: cover;
    background-position: center;

    video {
        position: absolute;
        z-index: 0;
        top: 0;
        left: 0;
        width: 100%;
        height: 550px;
        object-fit: cover;
    }

    .content-header {
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(0, 0, 0, .3) 0%, rgba(0, 0, 0, .6) 100%);
        backdrop-filter: blur(5px);
    }
}

.header-pages {
    width: 100%;
    height: 200px;
    background: url(../assets/img/bg-header.jpg);
    background-size: cover;
    background-position: center;
    padding: 0 0 30px 0;

    .content-header-pages {
        position: relative;
        z-index: 21;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        width: 100%;
        height: 200px;
        padding: 0 0 25px 0;
        background: rgba(0, 0, 0, 0.4);
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);
    }
}


/******************** Footer ********************/
.footer {
    width: 100%;
    background: $color-3;
    margin-top: 70px;

    .content-footer {
        width: 1400px;
        font-size: 14px;
        color: $white;

        .sub-title {
            font-size: 16px;
            font-weight: 600;
        }

        .img-app {
            width: 150px;
        }

        .link-category {

            a {
                color: $white;

                &:hover {
                    text-decoration: underline;
                }
            }
        }

        .logo-mika {
            width: 100px;
        }

        .developer {
            a {
                color: $yellow;

                &:hover {
                    text-decoration: underline;
                }
            }
        }

    }

}

.copyright {
    width: 100%;
    background: #080c16;
    padding: 10px;
    font-size: 12px;
    font-weight: 400;

    .content-copyright {
        width: 1400px;
        color: $white;
    }

    a {
        color: $yellow;

        &:hover {
            text-decoration: underline;
        }
    }
}


/******************** Navigation ********************/
.navigation {
    font-size: 15px;
    display: flex;
    justify-content: center;

    ul {
        display: flex;
        align-items: center;
        gap: 20px;

        a {
            text-decoration: none;
            color: $white;
            padding: 10px 15px;
            border-radius: 10px;
            transition: .2s ease;

            &:hover {
                background: #ffffff20;
            }
        }

    }

    .nav-active,
    .nav-active:hover {
        text-decoration: none;
        color: $black;
        padding: 10px 15px;
        border-radius: 10px;
        transition: .2s ease;
        background: #ffffff;
    }

    .no-button {
        text-decoration: none;
        color: $white;
        padding: 10px 15px;
        border-radius: 10px;
        transition: .2s ease;
        position: relative;
        cursor: pointer;

        &:hover {
            background: #ffffff20;
        }

        .menu-drop {
            width: 300px;
            padding: 20px;
            background: $white;
            border-radius: 10px;
            position: absolute;
            top: 45px;
            left: 50%;
            transform: translateX(-50%);
            box-shadow: 0 2px 15px rgba(0 0 0 /20%);

            ul {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 10px;

                a {
                    text-decoration: none;
                    color: $black;
                    padding: 10px 15px;
                    border-radius: 10px;
                    transition: .2s ease;
                    width: 100%;
                    text-align: center;

                    &:hover {
                        background: #00000020;
                    }
                }

            }
        }

        .nav-active-drop {
            text-decoration: none;
            color: $white;
            padding: 10px 15px;
            border-radius: 10px;
            transition: .2s ease;
            background: #00000050;
        }
    }

}



/******************** Box Search Formulaire ********************/
.box-search-form {
    position: relative;
    width: 1400px;

    .title {
        font-size: 45px;
        line-height: normal;
        font-weight: 600;
        color: $white;
    }

    .sub-title {
        font-size: 30px;
        line-height: normal;
        font-weight: 600;
        color: $white;
    }

}



/******************** Search Formulaire ********************/
.overlay-search-form {
    position: fixed;
    z-index: 19;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0 0 0/40%);
    backdrop-filter: blur(6px);
}

.search-form {
    z-index: 21;

    input {
        width: 700px;
        border: 0;
        outline: none;
        background: rgb(255, 255, 255);
        padding: 20px;
        border-radius: 50px;
        font-size: 16px;
        font-weight: 500;
        font-family: $font-1;
    }

    .instant-result {
        position: absolute;
        z-index: 20;
        top: 70px;
        left: 50%;
        transform: translateX(-50%);
        width: 700px;
        max-height: 450px;
        overflow: hidden;
        box-shadow: 0 2px 20px rgba(0 0 0 /20%);
        padding: 20px;

        .box-instant-result {
            max-height: 410px;
            padding: 0 10px 0 0;
            overflow-y: scroll;

            a {
                color: $color-3;
                padding: 5px 0;
                text-decoration: none;
            }

            .card-result {
                width: 100%;
                padding: 15px;
                border-radius: 10px;
                transition: .2s ease;

                &:hover {
                    background: rgba(0 0 0/5%);
                }

                img {
                    //width: 40px;
                    //height: 40px;
                    object-fit: cover;
                    border-radius: 5px;
                }

                .title {
                    font-size: 16px;
                    font-weight: 500;
                    color: $color-3;
                }

                .tag {
                    font-size: 13px;
                    color: $color-9;
                }

                .star {
                    font-size: 14px;
                    color: rgba(0 0 0 /10%);
                }

                .bi-arrow-right-circle {
                    font-size: 28px;
                    color: $color-10;
                    transform: translateX(-5px);
                    transition: .2s ease;
                }

            }

            .card-result:hover .bi-arrow-right-circle {
                color: $color-3;
                transform: translateX(0);
            }

        }

        .box-instant-result a:not(:last-child) {
            border-bottom: 1px solid rgba(0, 0, 0, 0.05);
        }

    }
}


/******************** Composant Card ********************/
.card {
    position: relative;
    width: 254px;
    height: 320px;
    border-radius: 20px;
    overflow: hidden;

    span img {
        //width: 100%;
        //height: 320px;
        object-fit: cover;
        transition: .2s ease !important;
    }

    .infos-recipe {
        position: absolute;
        z-index: 10;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 50%;
        padding: 20px 15px;
        color: $white;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 100%);

        .title {
            font-size: 16px;
            line-height: 18px;
            font-weight: 500;
        }

        .infos {
            font-size: 12px;
        }

        .star {
            font-size: 13px;
        }
    }

    .overlay-play {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        font-size: 50px;
        color: $white;
    }

    .btn-favoris,
    .btn-favoris-active {
        position: absolute;
        z-index: 10;
        top: 15px;
        right: 15px;
        font-size: 20px;
        color: $white;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background: rgba(255 255 255 /20%);
        backdrop-filter: blur(10px);
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid transparent;
        transition: .2s ease;

        //&:hover {
        //    border: 1px solid $white;
        //}
    }

    .btn-favoris-active {
        background: rgba(255 0 0 / 1);
    }

}

.card:hover span img {
    transform: scale(1.1);
}


/******************** Composant RecipeRandom ********************/
.recipe-random {
    margin: 60px 0 0 0;

    .title {
        font-size: 30px;
        font-weight: 600;
    }

}


/******************** Composant ListByCountry ********************/
.list-by-country {
    margin: 30px 0 0 0;

    .title {
        font-size: 30px;
        font-weight: 600;
    }

    .list-by-country-flag {
        gap: 30px 10px;

        a {
            color: $color-9;
            text-decoration: none;
            transition: .2s ease;

            &:hover {
                color: $black;
                transform: scale(1.1);
            }
        }
    }


    .box-flag {

        img {
            width: 85px;
            height: 85px;
            border-radius: 50%;
            border: 5px solid $white;
            box-shadow: 0 2px 5px rgba(0 0 0 /10%);
        }

        span {
            font-size: 14px;
        }
    }

}


/******************** Composant ListByCategory ********************/
.list-by-category {
    margin: 30px 0 0 0;

    .title {
        font-size: 30px;
        font-weight: 600;
    }

    .box-list-by-category {
        position: relative;
        width: 256px;
        height: 300px;
        border-radius: 20px;
        overflow: hidden;

        .infos {
            position: absolute;
            left: 0;
            bottom: 0;
            padding: 20px 15px;
            width: 100%;
            color: $white;
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 100%);

            .title {
                font-size: 20px;
            }

            .count-recipe {
                font-size: 16px;
            }
        }
    }
}


/******************** Composant ListByIngredient ********************/
.list-by-ingredient {
    margin: 30px 0 0 0;

    .title {
        font-size: 30px;
        font-weight: 600;
    }

    .container {
        gap: 15px;

        a {
            text-decoration: none;
            transition: .2s ease;
            border-radius: 10px;
            box-shadow: 0 2px 5px rgba(0 0 0 /10%);

            &:hover {
                transform: scale(1.1);
                box-shadow: 0 2px 10px rgba(0 0 0 /15%);
            }
        }
    }


    .box-ingredient {
        padding: 15px;
        border-radius: 10px;
        min-width: 80px;

        span {
            color: $color-3;
            font-size: 14px;
        }
    }
}


/******************** Composant ListBlog ********************/
.list-blog {
    margin: 30px 0 0 0;

    .title {
        font-size: 30px;
        font-weight: 600;
    }

    .card-article {
        overflow: hidden;
        border-radius: 20px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

        img {
            //width: 50%;
            //height: 100%;
            object-fit: cover;
        }
    }

    .content-card-article {
        padding: 30px;

        .title {
            font-size: 18px;
            font-weight: 600;
        }

        .date {
            font-size: 13px;
            color: $c-tertiary;
        }
    }

    .btn-display {

        i {
            opacity: .5;
            cursor: pointer;
            transition: .2s ease;

            &:hover {
                opacity: 1;
            }
        }
    }
}


/******************** Box Share ********************/
.box-share {
    position: fixed;
    z-index: 60;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0 0 0/40%);
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;

    .box {
        position: relative;
        width: 500px;

        .title {
            font-size: 28px;
            font-weight: 600;
            color: $black;
        }

        .btn-close-box-share {
            position: absolute;
            top: 20px;
            right: 20px;
            font-size: 26px;
            cursor: pointer;
        }

        input {
            width: 100%;
            border: 0;
            outline: none;
            padding: 5px 0;
            font-size: 14px;
            font-family: $font-1;
            background: transparent;
            color: $black;
            pointer-events: none;
        }

        .btn-copy-url {
            width: 30px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 18px;
            cursor: pointer;
        }

        .icons-rs-share {
            width: 100%;

            a {
                font-size: 35px;
            }

            .bi-facebook {
                color: #3b5998;
            }

            .bi-twitter {
                color: #00acee;
            }

            .bi-instagram {
                color: #b611ca;
            }

            .bi-envelope {
                color: #000000;
            }
        }
    }
}


/******************** Box Login ********************/
.box-login {
    position: fixed;
    z-index: 60;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0 0 0/40%);
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;

    .box {
        position: relative;
        width: 500px;

        .title {
            font-size: 28px;
            font-weight: 600;
            color: $black;
        }

        .btn-close-box-login {
            position: absolute;
            top: 20px;
            right: 20px;
            font-size: 26px;
            cursor: pointer;

        }
    }
}


/******************** Composant BlocNewsletterSub ********************/
.bloc-newsletter-sub {
    width: 100%;
    padding: 30px;
    border-radius: 20px;
    background-image: linear-gradient(0deg, rgba(255, 0, 0, .1) 0%, rgba(255, 0, 0, .1) 100%), url(../assets/img/bg-sub.png);
    background-size: cover;
    background-position: center;

    .title {
        font-size: 26px;
        font-weight: 600;
        //color: $black;
    }

    .sub-title {
        font-size: 18px;
        font-weight: 600;
    }

    .input-newsletter-sub {
        width: 400px;
        border: 0;
        outline: none;
        border-radius: 50px;
        background: $white;
        padding: 15px 20px;
    }

    .button-newsletter-sub {
        background: $c-primary-dark;
        color: $white;
        padding: 15px 20px;
        border-radius: 50px;
    }
}

/* Affichage mobile */
@media (max-width:1200px) {
    .top-bar {
        display: none;
    }

    .top-bar-mobile {
        display: flex;
    }

    .header {
        height: 450px;

        video {
            height: 450px;
        }
    }

    .box-search-form {

        .title {
            font-size: 26px;
        }

        .sub-title {
            font-size: 18px;
        }

    }

    .search-form {
        padding: 0 20px;

        input {
            padding: 15px;
            font-size: 14px;
        }

        .instant-result {
            top: 55px;
            padding: 10px;
        }
    }

    .recipe-random {

        .title {
            font-size: 22px;
            text-align: center;
        }

        a {
            width: 50%;
            display: flex;
            justify-content: center;
            margin: 0 0 5px 0;

        }
    }

    .card {
        width: 97%;
        height: 250px;

        .infos-recipe {
            height: 70%;

            .title {
                text-align: left;
            }
        }

        .overlay-play {
            top: 10px;
            left: 15px;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;

            i {
                font-size: 30px;
            }
        }
    }

    .list-by-country {

        .title {
            font-size: 22px;
            text-align: center;
        }
    }

    .list-by-category {

        .title {
            font-size: 22px;
            text-align: center;
        }

        a {
            width: 100%;
        }

        .box-list-by-category {
            width: 100%;
            height: 100px;

            img {
                object-fit: cover;
            }
        }
    }

    .list-by-ingredient {

        .title {
            font-size: 22px;
            text-align: center;
        }

        .container {
            gap: 5px;
        }
    }

    .list-blog {
        margin: 30px 0 0 0;

        .title {
            font-size: 30px;
            font-weight: 600;
        }

        .card-article {
            height: fit-content !important;

            .card-img {
                height: 200px !important;
            }
        }
    }

    .box-login {
        padding: 0 20px;
    }

    .bloc-newsletter-sub {
        background-size: contain;
        background-repeat: no-repeat;
        width: 100%;

        .input-newsletter-sub {
            width: 100%;
        }
    }

    .footer {

        .content-footer {

            .sub-title {
                font-size: 22px;
            }
        }
    }
}

