@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
$font-1: 'Poppins', sans-serif;


$c-primary: #ffe9f4;
$c-primary-dark: #9c5378;
$c-secondary: #cbf4ff;
$c-secondary-dark: #468fa3;
$c-tertiary: #909090;
$c-text: #303030;
$white: #ffffff;
$black: #000000;
$yellow: #ffc400;
$gray-clear: #f4f4f4;
$c-red: #ff0000;
$c-red-dark: #830000;

$bd-tablet: 1200px;
$bd-mobile: 600px;


$color-1: #61dafb;
$color-2: #e07f00;
$color-3: #282c34;
$color-4: #c4302b;
$color-5: #8b201d;
$color-6: #0086c4;
$color-7: #005c86;
$color-8: #f0f0f0;
$color-9: #909090;
$color-10: #b6b6b6;

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    background: $white;
    font-family: $font-1;
    font-size: 14px;
    font-weight: 500;
    color: $c-text;
}

li {
    list-style-type: none;
}

a {
    text-decoration: none;
}

input[type=text],
input[type=email],
input[type=password],
textarea {
    font-family: $font-1;
    font-size: 15px;
    background: $gray-clear;
    padding: 15px;
    border: 0;
    border-radius: 10px;
    outline: none;
}

button {
    outline: none;
    border: 0;
    font-family: $font-1;
    font-size: 16px;
}

::-webkit-scrollbar {
    /* Pour cibler la barre de défilement sur les navigateurs WebKit */
    width: 8px;
    background-color: #ffffff;
}

::-webkit-scrollbar-track {
    /* Pour cibler la partie de la barre de défilement qui représente la piste */
    background-color: $white;
}

::-webkit-scrollbar-thumb {
    /* Pour cibler la partie de la barre de défilement qui représente le curseur */
    background-color: #b4b4b4;
    border-radius: 10px;
}

::-webkit-scrollbar-button {
    /* Pour cibler les boutons de la barre de défilement */
    background-color: $white;
}


.btn {
    padding: 7px 14px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
    text-decoration: none;
    transition: .2s ease;
}

.btn-login {
    background: transparent;
    border: 1px solid $white;
    color: $white;

    &:hover {
        border: 1px solid $white;
        background: $white;
        color: $c-primary-dark;
    }
}

.btn-favorite-list {
    background: transparent;
    border: 1px solid $white;
    color: $white;

    &:hover {
        border: 1px solid $white;
        background: $c-primary-dark;
        border: 1px solid $c-primary-dark;
    }
}

.btn-primary {
    background: $c-primary;
    color: $c-primary-dark;

    &:hover {
        background: $c-primary-dark;
        color: $white;
    }
}

.btn-primary-border {
    background: transparent;
    border: 1px solid $c-primary-dark;
    color: $c-primary-dark;

    &:hover {
        background: $c-primary-dark;
        color: $white;
    }
}

.btn-gray-border {
    background: transparent;
    border: 1px solid $c-tertiary;
    color: $c-tertiary;

    &:hover {
        background: $c-tertiary;
        color: $white;
    }
}

.btn-red {
    background: $c-red;
    color: $white;

    &:hover {
        background: $c-red-dark;
        color: $white;
    }
}

.btn-red-border {
    background: transparent;
    border: 1px solid $c-red;
    color: $c-red;

    &:hover {
        background: $c-red;
        color: $white;
    }
}

.error-message {
    background: #ffcaca;
    color: #830000;
}

.warning-message {
    background: #ffec99;
    color: #834a00;
}

.box {
    background: $white;
    padding: 30px;
    border-radius: 20px;
    box-shadow: 0 2px 10px rgba(0 0 0/40%);
}

.main {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .page-content {
        width: 1400px;
        gap: 70px;
    }
}



@media (max-width:$bd-tablet) {
    .main {

        .page-content {
            width: 100%;
            padding: 0 10px;
            gap: 40px;
        }
    }
}

@media (max-width:$bd-mobile) {}