/******************** Recipe page ********************/
.page-recipe {

    .header-recipe {
        margin: 40px 0 0 0;

        .title {
            font-size: 40px;
            font-weight: 600;
            color: $black;
        }

        .tag-container-recipe {
            font-weight: 400;
            color: $c-tertiary;

            a {
                color: $c-tertiary;

                &:hover {
                    text-decoration: underline;
                }
            }
        }

        .tag {
            padding: 3px 8px;
            background: $c-primary;
            color: $c-primary-dark;
            border-radius: 50px;
            font-size: 12px;
            font-weight: 500;
        }
    }

    .content-recipe {
        width: 100%;

        img {
            width: 100%;
            height: 500px;
            object-fit: cover;
            border-radius: 20px;
        }

        .sub-title {
            font-size: 22px;
            font-weight: 600;
            color: $c-primary-dark;
        }

        .toggle-video {
            cursor: pointer;

            .overlay-play {
                position: absolute;
                z-index: 10;
                top: 0;
                left: 0;
            }

            i {
                font-size: 60px;
                color: $white;
                transition: .2s ease;
            }

            &:hover i {
                font-size: 80px;
            }
        }

        iframe {
            border: 0;
            border-radius: 20px;
            overflow: hidden;
        }

    }

    .info-recipe {
        margin: 20px 0 0 0;
        padding: 20px 0;
        border-top: 1px solid $gray-clear;
        border-bottom: 1px solid $gray-clear;

        .container-info-recipe {
            background: $white;

            i {
                font-size: 18px;
                color: $c-primary-dark;
            }

            span {
                font-size: 16px;
                font-weight: 500;
            }
        }

        .container-rating-recipe {

            .star-recipe {
                font-size: 18px;
                color: $yellow;
            }

            .rating-recipe {
                font-size: 16px;
            }

            .nbr-comment-recipe {
                font-size: 14px;
                color: $c-tertiary;
                text-decoration: none;

                &:hover {
                    text-decoration: underline;
                }
            }
        }

    }

    .list-ingredient .ingredient:not(:last-child) {
        border-bottom: 1px solid rgba(0 0 0/5%);
    }

    .ingredient {
        padding: 10px;
        border-radius: 5px;
        transition: .2s ease;

        &:hover {
            background: rgba(0 0 0/2%);
        }
    }

    .instruction-recipe {
        width: 100%;

        .header-instruction-recipe {
            margin: 40px 0 0 0;
            padding: 0 0 20px 0;
            border-bottom: 1px solid $gray-clear;

            .sub-title {
                font-size: 22px;
                font-weight: 600;
                color: $c-primary-dark;
            }
        }

        .step-instruction-recipe {
            width: 100%;
            margin: 10px 0;
            padding: 0 0 10px 0;

            .step-number-instruction-recipe {
                width: 60px;
                height: 60px;
                background: $gray-clear;
                border-radius: 10px;
                margin: 0 20px 0 0;
            }

            .step-number-instruction-recipe span:first-child {
                text-transform: uppercase;
                font-size: 14px;
                line-height: 14px;
                margin: 0 0 3px 0;
                opacity: .5;
            }

            .step-number-instruction-recipe span:last-child {
                font-size: 22px;
                line-height: 22px;
            }

            .text-instruction-recipe {
                width: 95%;
            }
        }

        .step-instruction-recipe:not(:last-child) {
            border-bottom: 1px solid $gray-clear;
        }
    }

}

.comments {
    width: 100%;
    margin: 40px 0 0 0;

    .sub-title {
        font-size: 22px;
        font-weight: 600;
        color: $c-primary-dark;
    }

    .no-found-comments {
        width: 100%;
        padding: 20px;
        //background: $gray-clear;
        //border-radius: 10px;
        border-top: 1px solid #f4f4f4;
        border-bottom: 1px solid #f4f4f4;

        i {
            font-size: 60px;
            opacity: .2;
        }

        span {
            font-size: 22px;
            font-weight: 600;
            opacity: .3;
        }
    }

    .bloc-comments {
        width: 100%;

        .header-bloc-comments {
            width: 100%;

            img {
                width: 50px;
                height: 50px;
                opacity: .5;
            }

            .author {
                font-size: 16px;
                font-weight: 600;
            }

            .date {
                font-size: 13px;
                color: $c-tertiary;
            }

            .star-bloc-comments {
                font-size: 16px;
                color: $yellow;
            }
        }

        .content-bloc-comments {
            padding: 10px;
            line-height: 26px;
        }
    }
}

.comments .bloc-comments:not(:last-child) {
    border-bottom: 1px solid #f4f4f4;
    padding: 0 0 20px 0;
}


