/******************** MyFavorites ********************/
.page-content-myfavorites {
    width: 1400px;
    margin: 40px 0 0 0;

    .title {
        font-size: 40px;
        font-weight: 600;
    }

    .delete-favorite {
        width: 100%;
        padding: 10px 20px;
        border-radius: 50px;
        font-size: 14px;
        margin: 10px 0 0 0;
        background: $white;
        color: #ff0000;
        cursor: pointer;
        transition: .2s ease;

        &:hover {
            background: #c00000;
            color: $white;
        }
    }

    .no-recipe-yet {
        width: 100%;

        i {
            font-size: 60px;
            opacity: .2;
        }

        .title {
            font-size: 24px;
            font-weight: 600;
            opacity: .4;
        }

        .sub-title {
            font-size: 16px;
            font-weight: 600;
            opacity: .4;
        }

        .description {
            opacity: .4;
        }
    }
}



