.loader {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 40;
    width: 100%;
    height: 100vh;
    background: #ffffff;
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader-site {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: $white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader-big,
.loader-big-gray {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: inline-block;
    border-top: 2px solid #FFF;
    border-right: 2px solid transparent;
    box-sizing: border-box;
    animation: loaderBtn .3s linear infinite;
}

.loader-big-gray {
    border-top: 2px solid #aaa;
}

@keyframes loaderBtn {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.bgIn {
    opacity: 0;
    animation: bgIn .2s ease-in-out forwards;
}

@keyframes bgIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.bgOut {
    opacity: 1;
    animation: bgOut .2s ease-in-out forwards;
}

@keyframes bgOut {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.contentIn {
    opacity: 0;
    transform: scaleX(0.7);
    animation: contentIn .3s ease-in-out forwards;
}

@keyframes contentIn {
    0% {
        opacity: 0;
        transform: scaleX(0.98);
    }

    50% {
        opacity: 1;
        transform: scaleX(1.02);
    }

    100% {
        opacity: 1;
        transform: scaleX(1);
    }
}

.contentOut {
    opacity: 1;
    transform: scaleX(1);
    animation: contentOut .3s ease-in-out forwards;
}

@keyframes contentOut {
    0% {
        opacity: 1;
        transform: scaleX(1);
    }

    50% {
        opacity: 1;
        transform: scaleX(1.02);
    }

    100% {
        opacity: 0;
        transform: scaleX(0.98);
    }
}

.openMenuMobileRight {
    width: 0;
    animation: openMenuMobileRight .3s ease forwards;
}

@keyframes openMenuMobileRight {
    0% {
        width: 0;
    }

    100% {
        width: 90%;
    }
}

.closeMenuMobileRight {
    width: 0;
    animation: closeMenuMobileRight .3s ease forwards;
}

@keyframes closeMenuMobileRight {
    0% {
        width: 90%;
    }

    100% {
        width: 0;
    }
}